.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 30px;

  @include vp--1239 {
    padding: 0 30px;
  }

  @include vp--1023 {
    padding: 0 24px;
  }

  @include vp--767 {
    padding: 0 15px;
  }

  &--no-padding {
    @include vp--767 {
      padding: 0;
    }
  }
}
